@use 'sass:map';
@import '../../../../styles/mixins.scss';

.connectCard {
  width: 329px;
  height: 51px;
  color: #ffffff;
  border-radius: 8px;
  cursor: pointer;
  position: relative;
  margin: 0px;
  padding: 12px 0px;
  padding-left: 72px;

  &:hover {
    span {
      color: black;
    }

    img {
      filter: drop-shadow(map.get($effect-styles, 'light'));
    }
  }

  .chain-info {
    justify-content: flex-start;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: row;
    color: #ffffff;
    gap: 24px;
  }
}

.connectCard-selected {
  .chain-info {
    color: black;

    img {
      filter: drop-shadow(map.get($effect-styles, 'light'));
    }
  }
}

.btn {
  &.connectCard-selected::before {
    background: map.get($gradient, 'light') !important;
  }
}

.disabled {
  .chain-info {
    span {
      color: map.get($text-line, 'dark-grey');
    }
  }
}

.comingSoon {
  position: relative;
  border: none;
  overflow: visible;
  border-radius: 6px;
  margin: 0 10px;
  background-color: #18252a;
  text-transform: none;

  & > div {
    font-weight: 500;
    font-size: 11px;
    line-height: 11px;
    background: map.get($gradient, 'standard');
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-transform: initial;
    padding: 4px 8px;
  }

  &:before {
    position: absolute;
    content: '';
    height: calc(100% + 2px);
    width: calc(100% + 2px);
    z-index: -1;
    left: -1px;
    top: -1px;
    border-radius: 6px;
    background: map.get($gradient, 'icon') !important;
  }
}
