@use 'sass:map';
@import '../../../../../../styles/mixins.scss';

.selectedButtonList {
  display: grid;
  grid-template-columns: 65px 65px 65px;
  position: absolute;
  right: 0px;
  top: 0px;

  .selectebutton {
    width: 58px;
    height: 36px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    &:hover {
      transition: 0.5s;
      color: black;
      background: map.get($miscellaneous, 'grey-blue');
      box-shadow: map.get($effect-styles, 'light');
    }
  }

  .selectedbutton {
    color: map.get($text-line, 'light-black');
    background: map.get($miscellaneous, 'light-blue');

    border: 1.5px solid map.get($miscellaneous, 'light-blue') !important;
  }

  .unselectedbutton {
    border: 1.5px solid map.get($miscellaneous, 'bright-blue') !important;
    color: map.get($miscellaneous, 'bright-blue');
  }
}
