@use 'sass:map';
@import '../../../../../../../styles/mixins.scss';

.overrall-info-container {
  margin: 12px 0px 0px !important;
  width: 100% !important;
  display: flex;
  flex-direction: row;
  gap: 12px;

  .data-grid-left {
    gap: 32px;
    padding: 24px;
    flex-grow: 1;
    border-radius: 8px;
    background: map.get($modal, 'dark-transparent');
    width: max-content;
  }

  .data-grid-right {
    padding: 16px 24px;
    flex-grow: 3;
    border-radius: 8px;
    background: map.get($modal, 'dark-transparent');
    gap: 48px;
    width: max-content;

    > div {
      // gap: 24px;
      width: max-content;
      justify-content: space-between;
    }
  }
}

.overallInfo-title {
  color: map.get($text-line, 'grey');
}

.overallInfo-content {
  &.headline-3 {
    margin-top: 16px;
  }
  &.headline-5 {
    margin-top: 12px;
  }

  .farming-pool {
    display: flex;
    flex-direction: column;
    gap: 6px;
  }
}

.overall-bar {
  width: 100%;
  height: 10px;
  padding-top: 0.5rem;
}

.bar-left {
  background: map.get($text-line, 'positive');
  height: 10px;
}

.bar-right {
  background: map.get($miscellaneous, 'dark-transparent-background');
  height: 10px;
}

.overall-bar div:first-of-type {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.overall-bar div:last-of-type {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.overall-bar-percentage {
  position: absolute;
  right: 0px;
  top: 0px;
}

.back-test-anlysis {
  background: map.get($miscellaneous, 'background-mask') !important;
}
