@use 'sass:map';
@import '../../../../styles/mixins.scss';

.graph-tooltip {
  background: map.get($modal, 'background');
  box-shadow: map.get($effect-styles, 'dark');
  border-radius: 10px;
  padding: 12px;

  display: flex;
  flex-direction: column;
  gap: 10px;

  .subtitle2 {
    color: map.get($text-line, 'light-grey');
  }

  .tooltips-dot {
    box-sizing: border-box;
    width: 6px;
    height: 6px;
    outline: 2px solid white;
    border-radius: 50%;
    display: inline-block;
    margin-bottom: 1px;
    margin-right: 8px;
  }

  .row-play {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 12px;
  }
}
