@use 'sass:map';
@import '../../../../styles/mixins.scss';

.avatar-img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-left: 12px;
  background: map.get($miscellaneous, 'dark-transparent-background');
  box-shadow: map.get($effect-styles, 'dark');

  img {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    margin-left: 2px;
    margin-top: 2px;
    background-color: #6d4aff;
  }
}
