@use 'sass:map';
@import '../../../../../../styles/mixins.scss';

.pdn-strategy-details {
  background: map.get($modal, 'background');
  border-radius: 8px;
  height: calc(100vh - 154px);
  width: calc(100vw - 260px);

  @media (min-width: 1440px) {
    width: 1180px;
  }

  @media (max-width: 1250px) {
    width: calc(100vw - 200px);
  }
}

.pdn-strategy-details-title {
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding: 24px;
  border-bottom: 1px solid map.get($text-line, 'light-grey-blue');

  .details-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    .action-button-group {
      button {
        width: 160px;
        height: 43px;
        padding: 8px 16px;
      }
      .action-withdraw {
        margin-left: 16px !important;

        &.btn-info::before {
          background: #516f7c;
        }
      }
    }
  }
}

.pdn-strategy-details-content {
  height: calc(
    100vh - 356px
  ); // 100vh - 154px - 154px(title part height) - 24*2 (padding)
  padding: 24px;
  overflow-y: scroll;
}

.modal-pdn-strategy-details {
  outline: 2px solid map.get($text-line, 'light-grey-blue');
}
