@use 'sass:map';
@import '../../../../../../styles/mixins.scss';

.mini-selectedButtonList {
  display: grid;
  grid-template-columns: 33% 33% 33%;
  width: 80%;
  margin-left: 10%;
  padding: 0px 0px 12px;

  .selectebutton {
    box-sizing: border-box;
    text-align: center;
    width: 35px;
    height: 24px;
    border-radius: 8px;
    border: 1px solid map.get($miscellaneous, 'bright-blue') !important;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin: auto;
    color: map.get($miscellaneous, 'bright-blue');

    &:hover {
      transition: 0.5s;
      color: map.get($text-line, 'light-black');
      background: map.get($miscellaneous, 'bright-blue');
      box-shadow: map.get($effect-styles, 'light');
    }
  }

  .selected {
    color: #090909;
    background: map.get($miscellaneous, 'light-blue');
    border-radius: 6px;
  }
}

.grid3-mini {
  margin: 16px 0px;
  display: grid;
  grid-template-columns: 33% 33% 33%;
  background: map.get($modal, 'dark-transparent');
  border-radius: 8px;
  padding: 8px 16px;

  .positive {
    color: map.get($text-line, 'positive');
  }

  .negative {
    color: map.get($text-line, 'negative');
  }
}

.center {
  text-align: center;
  padding: 8px 0px;
}

.return-chart {
  .subtitle-1 {
    margin-top: 16px;
  }
}

.line-graph-container {
  background: map.get($miscellaneous, 'dark-background') !important;
  border-radius: 8px;
  padding: 20px 12px;
}
