@use 'sass:map';
@import '../../../../styles/mixins.scss';

// Modal
.notification-modal {
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  display: flex;
  cursor: auto;
  align-items: center;
  justify-content: center;
  background-color: map.get($modal, 'dark-background');
  text-align: center;
  overflow-y: scroll;
  outline: 0;

  > div {
    position: absolute;
    top: 17.93%; // 180/1004
  }
}

.notification-pop-over {
  .MuiPaper-root.MuiDrawer-paper.MuiDrawer-paperAnchorBottom {
    display: flex;
    flex-direction: column;
    align-items: center;

    background: map.get($modal, 'background');
    border-radius: 24px 24px 0px 0px;
    padding: 24px;
    gap: 24px;

    color: white;
    width: auto;
  }

  .MuiPaper-root.MuiDrawer-paper > div {
    padding: 0px !important;
  }
}

.content-container {
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  padding: 32px;
  gap: 36px;
  position: relative;
  width: 547px;
  // height: 486px;
  background: map.get($modal, 'background');
  outline: 2px solid map.get($text-line, 'light-grey-blue');
  box-shadow: map.get($effect-styles, 'modal');
  border-radius: 8px;

  @media (max-width: 1199px) {
    padding: 0px;
    background: none;
    box-shadow: none;
    border-radius: 0px;
    border: none;
    outline: none;
    height: auto !important;
    width: auto !important;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
  }

  .cancel {
    position: absolute;
    top: 33px;
    right: 33px;
    cursor: pointer;
  }

  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;

    @media (max-width: 1199px) {
      gap: 8px;
    }

    .title {
      font-weight: 500;
      font-size: 32px;
      line-height: 48px;
      @media (max-width: 1199px) {
        font-weight: 500;
        font-size: 24px;
        line-height: 24px;
      }
    }

    .description {
      display: flex;
      color: map.get($text-line, 'light-white');
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      width: 321px;
      height: 48px;
      text-align: center;

      @media (max-width: 1199px) {
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        width: 291px;
        height: auto;
      }
    }
  }

  .access {
    width: 378px !important;
    height: 96px !important;

    @media (max-width: 1199px) {
      width: 291px !important;
      height: auto !important;
    }
  }
}

.confirm {
  width: 100%;

  button {
    margin-top: 0px;
    padding: 0px;
    width: 100%;
    font-size: 14px;

    &.btn.btn-lg::after {
      padding: 12px 16px;

      @media (max-width: 1199px) {
        font-weight: 500;
        font-size: 18px;
        line-height: 27px;
      }
    }
  }
}
