@import '../../../../../styles/mixins.scss';

.protocolContainer {
  display: flex;
  flex-direction: column;
  margin: 0.5rem;
  @include themify() {
    color: themed('textPrimary');
  }
  text-transform: initial;
  font-size: 14px;
  font-weight: 500;
  & > div {
    display: flex;
    margin: 0.1rem;
  }
  .imgContainer {
    display: flex;
    margin: 0.5rem;
    width: 25%;
    height: 24px;
    img {
      align-self: center;
      margin-right: 0.5rem;
    }

    .others {
      margin: 0;
      color: #ffffff;
      text-align: center;
      align-self: center;
      float: right;
      margin-left: '1rem';

      &:hover {
        cursor: pointer;
      }

      &:hover + .otherList {
        opacity: 1;
      }

      & > div {
        display: flex;
        margin: 0.1rem;
      }
    }
  }
  p {
    margin: 0;
    color: #ffffff;
    text-align: left;
    align-self: center;
    @media (max-width: 899px) {
      font-size: 12px;
    }
    .name {
      font-size: 14px;
    }
    .miniName {
      font-size: 12px;
    }
  }

  .fontValue {
    border-radius: 8px;
    font-size: 14px;
    background-color: #757575 !important;
    padding: 0 0.25rem;
    margin: auto 0.5rem;
    height: max-content;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0px 8px;
    gap: 10px;
  }

  .changePositive {
    font-size: 13px;
    line-height: 22px;
    color: #81ff8d;
  }
  .changeNegative {
    font-size: 13px;
    line-height: 22px;
    color: #ff8181;
  }

  .percent-bar {
    margin: 0.5rem;
    display: flex;
    flex-direction: row;
    width: 75%;
    .bar {
      align-self: center;
      height: 8px;
    }

    div.bar:first-of-type {
      border-top-left-radius: 10px;
      border-bottom-left-radius: 10px;
    }
    div.bar:last-of-type {
      border-top-right-radius: 10px;
      border-bottom-right-radius: 10px;
    }
  }

  .otherList {
    opacity: 0;
    display: flex;
    flex-direction: column;
    margin-top: 2rem;
    margin-left: -6rem;
    padding: 8px 16px;
    gap: 10px;

    width: 192px;
    height: max-content;
    left: 558px;
    top: 899px;

    background: #3a3a3a;
    border-radius: 8px;

    & > div {
      display: flex;
    }

    .percentValue {
      flex-direction: row;
      font-weight: 400;
      font-size: 14px;
      line-height: 22px;
      flex: 50%;
      text-align: right;
      align-self: center;
      color: #ffffff;
    }
  }
}
