@use 'sass:map';
@import '../../../../styles/mixins.scss';

.portClass {
  height: fit-content;
  @media (max-width: 1199px) {
    text-align: center;
    width: auto;
  }
  .title {
    color: map.get($text-line, 'light-grey');
    padding-bottom: 16px;

    @media (max-width: 1199px) {
      line-height: normal;
    }
  }

  .number-counter {
    span {
      bottom: 0px;
      background: map.get($gradient, 'icon');
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
    }
  }
}

.tvl-box {
  background: map.get($miscellaneous, 'card-background');
  box-shadow: map.get($effect-styles, 'dark');
  height: 100%;

  @media (max-width: 1199px) {
    box-shadow: none;
  }
}
