@use 'sass:map';
@import '../../../../../../styles/mixins.scss';

.info-row {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  align-items: center;
  justify-content: space-between;

  .fieldName {
    display: flex;
    text-align: left;
    color: map.get($text-line, 'light-grey');
    align-items: center;

    p {
      margin-block-start: 0px;
      margin-block-end: 0px;
    }

    .fontAvailable {
      color: map.get($text-line, 'positive');
      background: map.get($miscellaneous, 'blue-green');
      border-radius: 8px;
      padding: 6px;
      margin-left: 4px;
      text-align: center;
      display: inline-block;
    }

    .fontFull {
      color: map.get($text-line, 'grey');
      background: map.get($text-line, 'darkest-grey');
      border-radius: 8px;
      padding: 6px;
      margin-left: 4px;
      text-align: center;
      display: inline-block;
    }
  }

  .fieldValue {
    display: flex;
    flex-grow: 1;
    flex-direction: row-reverse;
    text-align: right;
    color: #ffffff;
    padding: 0;

    p {
      margin-block-start: 0px;
      margin-block-end: 0px;
    }
  }

  .postive {
    color: map.get($text-line, 'positive');
  }

  .negative {
    color: map.get($text-line, 'negative');
  }
}
