@use 'sass:map';
@import '../../../../../styles/mixins.scss';

.cardContainer {
  flex-direction: column;
  align-items: stretch;
  padding: 24px 16px;
  gap: 32px;

  position: relative;

  background: map.get($miscellaneous, 'strategy-hover');
  border-radius: 10px;
  margin-bottom: 16px;
  & > div:not(:first-child) {
    display: flex;
    padding: 16px 0 0 0;
  }

  .itemRow {
    display: flex;
    text-align: center;
    align-items: center;
  }
}
