@use 'sass:map';
@import '../../../../../../styles/mixins.scss';

.strategy-pop-over {
  .MuiPaper-root.MuiDrawer-paper {
    top: 98px;
  }
}
.strategyContainer {
  display: flex;
  flex-direction: column;
  padding: 24px !important;
  gap: 16px;
  color: #ffffff;
  background: map.get($modal, 'dark-background');
  border-radius: 24px 24px 0px 0px;
  height: 100%;

  .tabNav {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 4px;
    gap: 6px;
    height: 45px;
    background: map.get($miscellaneous, 'blue-green');
    border-radius: 66px;

    flex: none;
    order: 0;
    align-self: stretch;
    flex-grow: 0;

    li {
      width: 25%;
      list-style: none;
      text-align: center;
      cursor: pointer;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      padding: 0.35rem 0rem;
      gap: 10px;

      height: 37px;
      flex: none;
      order: 0;
      flex-grow: 1;

      &.active {
        font-weight: 600;

        background: map.get($miscellaneous, 'strategy-hover');
        border-radius: 66px;

        .tabName {
          color: map.get($text-line, 'white');
        }
      }

      .tabName {
        display: flex;
        align-items: center;
        text-align: center;
        color: map.get($text-line, 'grey');
      }
    }
  }

  .itemRow {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    align-items: center;
  }

  .positive {
    color: map.get($text-line, 'positive');
  }

  .negative {
    color: map.get($text-line, 'negative');
  }

  .strategy-content {
    display: flex;
    flex-direction: column;
    gap: 18px;
  }
}
